* {
  margin: 0px;
}
li{
  list-style: none;
}
.font_white{
  color: white;
}
.fa{
  padding: 7px 20px;
}
.font_bolder_2x{
  font-size: 2rem;
  font-weight: bolder;
}
/* 공용 */
.container{
  display: flex;
  justify-content: space-between;
  
}
.nav{
  width: 100vw;
  height: 50px;
  background-color: black;
  color: white;
}
.bgslider{
  position: relative;
  z-index: 10;
  background-color: black;
  transition: transform 0.5s;
  transform: translateY(-120%);
}
.slider_on{
  transform: translateY(-120%);
}
.slider_off{
  transform: translateY(0%);
}
.slidecontainer{
  width: 100vw;
  height: 95vh;
  position: relative;
  display: flex;
  flex-direction: column;

}
.slidecontainer_items{
  border-bottom: 1px solid gray;
  margin:0px 80px;

}
.slideitem{
  padding:20px;
  font-size: 1rem;
  font-weight: bolder;
  text-align: left;
  color: white;  
}



@media screen and (max-width:576px) {
  .slide_on{
    width: 100%;
    max-width: none;
  }
  
}